.neon {
  text-shadow: 0 0 5px orange, 0 0 15px orange, 0 0 20px orange, 0 0 40px orange, 0 0 60px red, 0 0 10px #ff8d00, 0 0 98px red;
  color: #fff6a9;
  text-align: center;
  font-family: Sacramento, cursive;
  animation: blink 12s infinite;
}

@keyframes blink {
  20%, 24%, 55% {
    color: #111;
    text-shadow: none;
  }

  0%, 19%, 21%, 23%, 25%, 54%, 56%, 100% {
    text-shadow: 0 0 5px red, 0 0 15px red, 0 0 20px red, 0 0 40px red, 0 0 60px red, 0 0 10px red, 0 0 98px red;
    color: red;
  }
}

.main-my-photo {
  width: 80%;
  height: 85%;
  border-radius: 50%;
  margin-top: 5%;
}

